  .menu-list {
    gap: 32px;
  }
}

@media screen and (max-width: 767px) {
  .menu-list {
    display: none;
  }
}

.menu-link {
  color: #b7b7b7;
  font-size: 14px;
  line-height: 1.14286;
}

.mobile-menu-open {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
  line-height: 0;
}

@media screen and (min-width: 768px) {
  .mobile-menu-open {
    display: none;
  }
}

.mobile-menu {
  z-index: 1000;
  width: 100%;
  height: 100%;
  max-height: 796px;
  background-color: #171717;
  position: fixed;
  top: 0;
  left: 0%;
}

@media screen and (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}

.mobile-menu-container {
  height: 100%;
  padding-top: 8px;
  position: relative;
}

.mobile-menu.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

.js-menu-container {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
}

.js-menu-container.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

.mobile-menu-close-btn {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin-bottom: 40px;
  padding: 0;
  line-height: 0;
}

.mobile-menu-item:not(:last-child) {
  margin-bottom: 24px;
}

.mobile-menu-link {
  color: #fff;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.14;
}

.current {
  color: #f87719;
}

body {
  background-attachment: fixed;
}

